import composeRefs from '@seznam/compose-react-refs';
import React, { useCallback, useEffect, useRef, useState } from 'react';
import { useIsMounted } from 'usable-react';
import { ResizePayload, watchResize } from 'watch-resize';
import { cssTimeToMilliseconds } from '../../libs/css-time-to-milliseconds';
import { forwardRefWrapper } from '../../libs/forward-ref-wrapper';
import { IntrinsicElementProps } from '../../types/utility';
import { useSSR } from '../ssr-provider';

interface TransitionStyles {
  transition: string;
  height: string;
  width: string;
  willChange: string;
}

interface TransitionConfig {
  duration?: string;
  timingFunction?: string;
  delay?: string;
}

interface AutoResizingDivProps extends Omit<IntrinsicElementProps<'div'>, 'onResize'> {
  initialDelay?: number;
  transition?: boolean | TransitionConfig;
  applyCSSVariables?: {
    element?: HTMLElement;
    transitionVar?: string;
    heightVar?: string;
    widthVar?: string;
  };
  onResize?: (
    payload: Omit<ResizePayload<HTMLElement>, 'destroy'> & {
      transitionStyles: TransitionStyles;
    },
  ) => void;
  onAnimationFrame?: FrameRequestCallback;
  observerOnly?: boolean;
  autoWidth?: boolean;
  autoHeight?: boolean;
}

